  display: block;
}
.soc a {
  position: relative;
  height: 96px;
  height: 9.6rem;
  width: 96px;
  width: 9.6rem;
}

.icon-8:hover,
.icon-13:hover,
.icon-15:hover,
.icon-17:hover,
.icon-28:hover {
  border-radius: 100%;
  color: black;
  fill: black;
  -webkit-transform: scale(1.25);
  transform: scale(1.25);
  transition: background-color 0.5s, -webkit-transform 0.5s ease-out;
  transition: background-color 0.5s, transform 0.5s ease-out;
  transition: background-color 0.5s, transform 0.5s ease-out,
    -webkit-transform 0.5s ease-out;
}

